import React from 'react'

import {Layout, Link} from "actb"

import { List, Avatar } from 'antd'
import {distinctColors} from 'actl'


const Panel = Layout.Panel


function ItemAvatar({item}){

    return <Avatar size="large" style={{backgroundColor:item.backgroundColor}}>{ item.keyword }</Avatar>
}

function preparePosts(posts){

  const v1 = posts.map(item => {
    const keywords = (item.frontmatter.keywords || "ACT").split(",")
    const keyword = keywords[0].trim()
    return {
      keyword, 
      title:item.frontmatter.title, 
      excerpt:item.excerpt, 
      slug:item.slug
    }
  })

  return distinctColors.distinctBackgroundColors(v1)
}

export default function PostsPage({pageContext}){
  const context = Object.assign({frontmatter:{}}, pageContext)
  const props = Object.assign({}, {layout:context.layout}, context.frontmatter)

  const posts = preparePosts(pageContext.posts)

  return (
    <Layout {...props}>
      <Panel>
        <List
            itemLayout="horizontal"
            dataSource={posts}
            renderItem={item => (
            <List.Item>
                <List.Item.Meta
                avatar={<ItemAvatar item={item}/>}
                title={<Link to={item.slug}>{item.title}</Link>}
                description={ item.excerpt }
                />
            </List.Item>
            )}
        />          
      </Panel>
    </Layout>
  )
}