import distinctColors from 'distinct-colors'

export function distinctBackgroundColors(items){
    const unique = items.reduce((context, v)=>{
        if (v.keyword in context.keywords){
        return context
        }
        context.keywords[v.keyword] = context.count++
        return context
    }, {count:0, keywords:{}})
    const palette = distinctColors({count: unique.count > 5 ? unique.count:5})

    return items.map(v=>{
        v["backgroundColor"] = palette[unique.keywords[v.keyword]].css()
        return v
    })
}

export default {
    distinctBackgroundColors
}